.md2-calendar-header {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  line-height: 48px; }
  .md2-calendar-header .md2-calendar-month-year {
    width: 100%; }
  .md2-calendar-header button {
    display: inline-block;
    width: 48px;
    height: 48px;
    padding: 12px;
    outline: none;
    border: 0;
    cursor: pointer;
    background: transparent; }

.md2-calendar-week-day {
  display: inline-block;
  width: 40px;
  line-height: 40px;
  margin: 1px;
  color: rgba(0, 0, 0, 0.38);
  text-align: center;
  text-transform: uppercase; }

.md2-calendar-date {
  display: inline-block;
  width: 40px;
  line-height: 40px;
  margin: 1px;
  text-align: center;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid transparent; }
  .md2-calendar-date.md2-calendar-date-today {
    border-color: #106cc8; }
  .md2-calendar-date.md2-calendar-date-focused, .md2-calendar-date:hover {
    background: #e0e0e0; }
  .md2-calendar-date.md2-calendar-date-selected:not(.md2-calendar-date-disabled) {
    background: #106cc8;
    color: white;
    border-color: transparent; }
  .md2-calendar-date.md2-calendar-date-disabled {
    cursor: default;
    pointer-events: none;
    opacity: 0.5; }
